.card{
 display: flex;
 flex-direction: column;
 background-color:turquoise;

 border: 1px solid grey;
 border-radius: 5px;

 padding: 25px;
 cursor: pointer;

 backface-visibility: hidden;
 
 transform: translateZ(0);
 transition: transform 0.5s ease-out;

}

.card:hover {
    transform: scale(1.05);
  }
  